#banner {
  background-color: var(--detail-light);

  height: 100px;
  padding: 10px;
  margin-top: 60px;
}

#mainLogo {
  height: 100%;
  width: auto;

  float: left;
  margin-left: 120px;
}

#banner .links {
  --font-size: 1.8rem;
  margin-right: 120px;
  width: 600px;
}

#banner .links .qButton {
  min-width: 80px;
  height: 60px;

  line-height: 60px;
  border-left: solid #e8e8e8 6px;
  border-right: solid #e8e8e8 6px;

  margin-left: -3px;
  margin-right: -3px;
}

#coursesTitle {
  color: var(--font-color);
  font-family: var(--font-typeface);
  font-size: var(--font-size);
  font-weight: bold;
}

#footer {
  height: 30px;
  width: 100%;

  background-color: var(--detail-light);
}

#sidebarContainer {
  width: 20%;
  margin-right: 32px;
  margin-left: 108px;
}

#sidebar {
  padding-top: 24px;

  width: 100%;
  height: 100%;
  background-color:var(--detail-light);
}

#coursesHeader {
  color: var(--font-color);
  font-size: 1.8rem;
  font-family: var(--font-typeface);
  font-weight: bold;

  padding: 8px 24px 8px 24px;
  border-top: solid 1.5px var(--background-light);
  border-bottom: solid 1.5px var(--background-light);
}

.courseLabel {
  padding-top: 4px;
  padding-bottom: 4px;
  padding-left: 24px;
}

.courseLabel[type="button"] {
  font-size: 1.4rem;
  -webkit-appearance: none;
    vertical-align: middle;
    text-align: left;
    border: 0;
    -webkit-transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0 0 0 18px;
}

li .qButton {
  text-align: left;
}

.progressBar * {
  color: var(--font-color);
  font-family: var(--font-family);
  font-weight: bold;
}

.progressBar {
  position: relative;

  width: 300px;
  border: solid 2px var(--detail-light);
}

.progressFill {
  position: relative;
  text-align: left;
  height: 30px;
  line-height: 30px;
  overflow: hidden;
}

.progressBarText {
  position: absolute;
  line-height: 30px;

  top: 0px;
  left: 12px;
}

.progressBarPercent {
  position: absolute;
  line-height: 24px;
  right: 8px;
  top: 4px;
}

.textOverlay {
  color: var(--background-light);

  z-index: 2;
  white-space: nowrap;
}

#dashHeader {
  margin-top: 24px;
  margin-bottom: 16px;
}

#dashWelcomeContainer {
  padding: 10px 24px 5px 24px;
  border-left: solid 6px var(--detail-light);
  border-right: solid 6px var(--detail-light);
  background-color: var(--background-medium);
}

#dashWelcome {
  font-size: 2rem;
  font-weight: 600;
  color: var(--background-light);
}

.dashboardNote {
  margin-bottom: 16px;
}

.dashboardNote a {
  color: var(--background-dark);
}

.dashboardNote a:hover {
  color: grey;
}

.notification {
  padding: 12px 24px 16px 24px;
}

.notificationDescription {
  min-width: 300px;
  padding: 4px 8px 4px 8px;
  color: var(--font-color);
  background-color: var(--background-medium);

  box-shadow: 2px 2px 4px var(--detail-light);
  z-index: 2;
}

.notification .progressBar {
  box-shadow: 2px 2px 4px var(--background-medium);
}

.notification > .horDivider {
  margin-top: 8px;
  margin-bottom: 8px;

  box-shadow: 0 1px 2px var(--background-medium);
}

.notificationMeta {
  color: var(--background-dark);
}

html, body {
  --background-light: #e8e8e8;
  --background-medium: #c4c4c4;
  --background-dark: #aeaeae;
  --detail-light: LightSteelBlue;
  --detail-dark: #4c84a3;
  --font-family: "Helvetica";
  --font-color: #e8e8e8;
  --font-border: #a8a8a8;
  --font-size: 1.2rem;

  background-color: var(--background-light);
}

html, body, #root, #appContainer {
  width: 100vw;
  height: 100vh;
}

body {
  margin: 0;
}

a, a:hover {
  text-decoration: none;
}

.qButton {
  text-align: center;
  padding-left: 24px;
  padding-right: 24px;

  color: var(--font-color);
  font-family: var(--font-typeface);
  font-size: var(--font-size);
  font-weight: bold;
}

.qButton:hover {
  background-color: #95a6bca3;
  cursor: pointer;
}

.textshadow {
  text-shadow: 0 0 6px var(--font-border);
}

.figure {
  color: var(--detail-light);
  font-weight: bold;
  text-shadow: 1px 1px 1px var(--background-dark);
}

#pageContentRow {
  padding-top: 12px;
  padding-bottom: 12px;
  background-color: var(--background-light);
}

#pageContentWrapper {
  position: relative;
}

#pageContent {
  height: 100%;
  overflow-y: scroll;
}

#pageContentShadow {
  width: 100%;
  height: 80px;

  position: absolute;
  bottom: 0px;
  left: 0px;

  z-index: 3;
  box-shadow: inset -12px -32px 32px -16px var(--background-light);
}

